/**@type{ import('../typeing/phaser') } */
var config={
    type:Phaser.AUTO,
    height:600,
    width:800,
    physics:{
        default:"arcade",
        arcade:{
            gravity:{y:300},
            debug:false
        }
    },
    scene:{
        preload:preload,
        create:create,
        update:update
    }
}

var game=new Phaser.Game(config)
function preload(){
    this.load.image('sky','../images/sky.png')
    this.load.sprite('dude','../images/dude.png',{frameWidth:32,frameHeight:48})
    this.load.image('platform','../images/platform.png')
    this.load.image('bomb','../images/bomb.png')
}
function create(){
    this.add.image(400,300,'sky')

}
function update(){

}